import React from "react";
import {
    Wifi
} from "@cvccorp-components/icons";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import Stops from "../../Stops";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import FilterChip from "../FilterChip";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    amenitiesText: { 
        display: "block", 
        marginBottom: theme.spacing(1) 
    },
    stop: { 
        marginBottom: theme.spacing(1),
        width: "100%"
    },
}));

type FilterChipStoryProps = {
    mode: string
    brand: BrandType
    matrix: boolean
    isAmenities: boolean
    isLuggage: boolean
    disabled: boolean
    label: string,
    icon: boolean,
    stops: number,
}

const FilterChipStory = ({
    mode, brand, matrix, isAmenities, isLuggage, disabled, label, icon, stops
}: FilterChipStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="FilterChip">
                <Typography variant="h3">FilterChip</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <FilterChip
                        classes={classes[mode]}
                        matrix={matrix}
                        isAmenities={isAmenities}
                        isLuggage={isLuggage}
                        disabled={disabled}
                        onClick={() => undefined}
                        label={
                            <span>
                                {!!stops && <Stops className={classes.stop} qtd={stops} />}
                                {icon && <Wifi size="medium" />}
                                <span className={classes.amenitiesText}>{label}</span>
                            </span>
                        }
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        label: "Label",
        icon: false,
        disabled: false,
        stops: 0,
        matrix: false,
        isAmenities: false,
        isLuggage: false,
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        icon: { type: "boolean" },
        matrix: { type: "boolean" },
        isAmenities: { type: "boolean" },
        isLuggage: { type: "boolean" },
        disabled: { type: "boolean" },
        label: { type: "string" },
        stops: { type: "number" }
    }
}

storiesOf("Components/Filtros/FilterChip", module)
    .add("Default", (args) => <FilterChipStory {...args}/>, argsControls);
